@import '../../styles/vars';

.screen {

  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: $content-height;

  animation-fill-mode: both;
  animation-duration: .3s;

  &--enter-active {

    animation-name: fadeInRight;

  }

  &--enter-done {

    opacity: 1;

  }

  &--exit {

    pointer-events: none;

  }

  &--exit-active {

    animation-name: fadeOutLeft;
    pointer-events: none;

  }

  &--exit-done {

    opacity: 0;
    pointer-events: none;

  }

  &--static-entrance {

    &.screen--enter-active {

      animation: none;

    }

    &.screen--enter-done {

      opacity: 1;

    }
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeOutLeft {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }
}

@keyframes fadeOutRight {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
  }
}
