@import '../styles/vars';

.payment-accepted {

  &__product {

    width: 600px;
    margin: 0 auto;
    text-align: center;

  }

  &__product-image {

    margin: 80px 0 0 0;
    width: 600px;

  }

  &__product-name {

    padding: 0 35px;
    margin-bottom: 50px;
    font-size: 47px;
    font-family: $font-regular;
    line-height: 60px;

  }

  &__product-description {

    margin: 0 35px;
    padding: 0 0 42px 0;
    border-bottom: 2px solid;
    font-size: 23px;
    font-weight: light;
    line-height: 34.5px;

  }
}
