@import '../styles/vars';

.payment-declined {

  &__image {

    display: block;
    position: relative;
    width: auto;
    top: 50%;
    transform: translateY(-50%);
    animation: bob ease-in-out 5s infinite;

  }
}

@keyframes bob {

  from,
  to {
    transform: translateY(calc(-50%));
  }

  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translateY(calc(-50% - 10px));
  }

  20%,
  40%,
  60%,
  80% {
    transform: translateY(calc(-50% + 10px));
  }
}
