@import '../styles/vars';
@import '../styles/mixins';

.purchase {

  &__product {

    width: 600px;
    margin: 0 auto;
    text-align: center;

  }

  &__product-image {

    width: 600px;

  }

  &__product-name {

    padding: 0 35px;
    margin-bottom: 50px;
    font-size: 47px;
    font-family: $font-regular;
    line-height: 60px;

  }

  &__product-purchase {

    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 0 268px;
    padding: 42px 0;
    border-bottom: 1px solid;
    font-size: 25px;
    transition-delay: 1s;

    div {

      &:nth-child(1) {

        text-align: left;

      }

      &:nth-child(2) {

        text-align: right;

      }
    }
  }

  &__price {

    display: grid;
    align-items: center;
    grid-column: 2;
    margin: 60px 0 27px 0;
    padding: 0 80px 0 0;
    font-size: 80px;

    p {

      font-size: 22px;
      text-align: right;

    }
  }

  &__instructions {

    margin: 80px 240px 40px;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    background-color: white;

  }

  &__payment-options {

    text-align: center;
    margin-top: 100px;

  }

  &__actions {

    text-align: center;

    Button {

      margin: 80px 0;

    }
  }
}
