@import '../styles/vars';

.products {

  margin-top: 260px;

  &__product {

    text-align: center;

  }

  &__product-image,
  &__product-name,
  &__product-description,
  &__product-purchase {

    transition: opacity ease-out .4s;

  }

  &__product-purchase {

    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 0 35px;
    font-size: 35px;
    transition-delay: 1s;

    div {

      padding: 20px 0;

      &:nth-child(1) {

        text-align: left;

      }

      &:nth-child(2) {

        text-align: right;

      }
    }
  }

  &__product-image {

    width: 450px;

  }

  &__product-name {

    padding: 0 35px;
    margin-bottom: 50px;
    font-size: 47px;
    font-family: $font-regular;
    line-height: 60px;

  }

  &__product-description {

    margin: 0 35px;
    padding: 0 0 42px 0;
    border-bottom: 2px solid;
    font-size: 23px;
    font-weight: light;
    line-height: 34.5px;
    text-align: left;

  }

  *[aria-hidden="true"] {

    .products__product-image {

      opacity: .5;

    }

    .products__product-name,
    .products__product-description,
    .products__product-purchase {

      opacity: 0;

    }

    .products__product-purchase {

      transition-delay: 0s;

    }
  }
}
