@import '../styles/vars';

.digital-receipt {

  height: 100%;

  &__product {

    width: 600px;
    margin: 0 auto;
    text-align: center;

  }

  &__product-image {

    margin: 50px 0 0 0;
    width: 600px;

  }

  &__product-name {

    padding: 0 35px;
    margin-bottom: 50px;
    font-size: 47px;
    font-family: $font-regular;
    line-height: 60px;

  }

  &__product-description {

    margin: 0 35px;
    margin-bottom: 60px;
    font-size: 30px;
    font-weight: light;
    line-height: 34.5px;

  }

  &__interface {

    display: grid;
    grid-template-rows: max-content 1fr max-content;
    justify-items: center;
    text-align: center;

  }

  h1 {

    font-size: 64px;

  }

  &__input {

    width: 718px;
    padding: 15px 0;
    border-radius: 0;
    border: 0;
    margin-bottom: 54px;
    border-bottom: 1px solid black;
    font-family: $sans-serif;
    font-size: 30px;
    text-align: center;
    pointer-events: none;

    &--error {

      color: $red;

      &::placeholder {
        color: $red;
      }
    }
  }

  &__actions {

    text-align: center;

    Button {

      display: inline-block;
      width: 265px;
      margin: 70px 7.5px;

    }
  }

  &--disabled {

    pointer-events: none;

  }
}
