@import '../styles/vars';

.collect-item {

  &__product {

    width: 600px;
    margin: 0 auto;
    text-align: center;

  }

  &__product-image {

    margin: 80px 0 0 0;
    width: 600px;

  }

  &__product-name {

    padding: 0 35px;
    margin-bottom: 50px;
    font-size: 47px;
    font-family: $font-regular;
    line-height: 60px;

  }

  &__product-description {

    margin: 0 35px;
    margin-bottom: 60px;
    padding: 0 0 42px 0;
    border-bottom: 1px solid black;
    font-size: 23px;
    font-weight: light;
    line-height: 34.5px;

  }

  &__arrow-wrapper {

    transform: translateX(30px);

  }

  &__arrow {

    display: block;
    position: relative;
    width: auto;
    margin: 0 auto;
    transform: translateY(0%);
    animation: bob ease-in-out 5s infinite;

  }

  &__actions {

    text-align: center;

    p {

      font-size: 30px;

    }

    Button {

      display: inline-block;
      width: 265px;
      margin: 40px 7.5px;

    }
  }
}


@keyframes bob {

  from,
  to {
    transform: translateY(calc(20%));
  }

  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translateY(calc(20% - 10px));
  }

  20%,
  40%,
  60%,
  80% {
    transform: translateY(calc(20% + 10px));
  }
}
