@import '../../styles/vars';

.button {

  max-width: 545px;
  width: 100%;
  padding: 16px;
  border-radius: 0;
  outline: 0;
  border: 0;
  font-size: 26px;
  font-family: $sans-serif;
  background-color: rgba(0, 0, 0, 0);
  color: black;

  &--light {

    border: 2px solid;
    background-color: rgba(0, 0, 0, 0);
    color: $black;

  }

  &--unstyled {

    font-family: $serif;
    font-size: 60px;
    color: $black;
    background-color: rgba(0, 0, 0, 0);

  }

  &--error {

    color: $black;
    background-color: $orange;

  }

  &--black {

    background-color: black;
    color: white;

  }

  &--nav {

    width: auto;
    padding: 40px;
    font-size: 38px;
    background-color: rgba(0, 0, 0, 0);
    color: black;

  }

  &:disabled {

    opacity: .5;

  }
}
