@import '../../styles/vars';
@import '../../styles/mixins';

.modal {

  position: absolute;
  width: 100%;
  height: $content-height;
  top: 0;
  left: 0;
  background-color: $cream;
  overflow: hidden;
  z-index: 2;
  transition: height .25s cubic-bezier(.51,.26,.86,1.06);

  &__inner {

    display: grid;
    align-items: center;
    justify-items: center;
    height: $content-height;

  }

  &__box {

    width: 940px;
    padding: 40px;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0);
    text-align: center;
    color: $cream;

    h1 {

      font-family: $font-light;
      font-size: 64px;
      color: $black;

      @include fadeInAndDown();

    }

    p {

      font-family: $font-light;
      color: $black;
      padding: 0 100px;

    }

    Button {

      margin: 40px;

    }
  }

  &--enter {

    height: 0;

    &:not(.modal--enter-active) {

      background-color: $cream !important;

    }
  }

  &--enter-active {

    height: $content-height;

  }

  &--exit {

    height: $content-height;

    background-color: $cream !important;

  }

  &--exit-active {

    height: 0;

    background-color: $cream !important;

  }
}
