@import 'styles/fonts.scss';
@import 'styles/vars.scss';
@import '../node_modules/animate.css/animate.min.css';

html,
body {

  position: relative;
  height: 100%;
  overflow: hidden;

}

body {

  margin: 0;
  font-family: $font-regular;
  font-size: 40px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background:
    linear-gradient(180deg, rgba(219, 169, 116, 0.2) 0%, rgba(255, 255, 255, 0) 50%),
    #FFFFFF;

  * {

    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0);

  }
}

.static-logo {

  position: absolute;
  top: 1690px;
  left: 50%;
  transform: translateX(-50%);

}

main {

  position: relative;

}

h1 {

  font-family: $sans-serif;
  font-size: 60px;
  text-align: center;
  margin: 60px 0 27px 0;

}

h2 {

  font-family: $serif;
  font-size: 20pt;
  line-height: 22px;

}

#root {

  position: relative;

}

/*

  Global animations

*/

@keyframes fadeInDownShort {
  from {
    opacity: 0;
    transform: translate3d(0, -80px, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

/*

  Library Overrides

*/

/*

  Note: Using !important sucks. In the future I'd like to find an elegant way
  to override external library styles in a CSS Modules fashion. For now, let's
  be naughty...

*/

.carousel {

  .slide {

    background: rgba(0, 0, 0, 0) !important;

  }

  .carousel-status {

    display: none !important;

  }

  .control-dots {

    .dot {

      width: 200px !important;
      height: 6px !important;
      border-radius: 3px !important;
      border: 1px solid $blue !important;
      box-shadow: none !important;
      opacity: 1 !important;

      &.selected {

        background-color: darken($blue, 5%);

      }
    }
  }
}

.hg-theme-default {

  font-family: $sans-serif !important;
  background-color: rgba(0, 0, 0, 0) !important;

  .hg-row {

    display: block !important;

  }

  .hg-button {

    display: inline-block !important;
    min-width: 70px !important;
    max-width: auto !important;
    height: 70px !important;
    border: 1px solid rgba(0, 0, 0, 0.0846354);
    border-radius: 8.55px;
    color: $black !important;
    box-shadow: none !important;
    padding: 5px 20px !important;
    line-height: 70px !important;
    font-size: 22px !important;

    &.hg-standardBtn[data-skbtn="@"],
    &.hg-standardBtn[data-skbtn=".com"] {

      max-width: none !important;

    }
  }
}
