@import '../../styles/vars';

.generic-screen {

  display: grid;
  grid-template-rows: max-content 1fr max-content;
  justify-items: center;
  height: 100%;
  text-align: center;

  &__text {

    margin: 242px 80px 40px 80px;

  }

  &__heading {

    margin: 0;
    font-size: 80px;
    font-family: $font-light;
    color: $black;

  }

  p {

    max-width: 700px;
    padding: 0 150px;
    color: $black;
    font-family: $font-regular;

  }

  &__content {

    box-sizing: border-box;
    height: 100%;
    padding: 50px 40px 80px 40px;

  }

  &__buttons {

    box-sizing: border-box;
    grid-gap: 80px;
    width: 100%;
    padding: 0 100px;
    margin-bottom: 40px;

    Button {

      display: inline-block;
      max-width: 265px;
      margin: 0 7.5px 40px 7.5px;

    }
  }
}
