@import '../../styles/vars';

.header {

  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr 1fr;
  position: absolute;
  width: 100%;
  padding: 40px;
  z-index: 2;
  transition: opacity ease-out .5s;

  &__left,
  &__right {

    display: block;

  }

  &__right {

    text-align: right;

  }

  &--hide {

    pointer-events: none;
    opacity: 0;

  }
}
