@import '../styles/vars';

.landing {

  position: relative;
  width: 1080px;
  height: 1920px;

  &__background {

    position: absolute;
    width: 100%;
    height: 1920px;
    top: 0;
    left: 0;
    background-color: #e3e3e3;

  }

  &__logo {

    position: absolute;
    width: 100%;
    height: 1920px;
    top: 0;
    left: 0;
    background-image: url('../images/haeckels-logo-white.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 300px auto;

  }

  &__tts {

    position: absolute;
    width: 400px;
    top: 0;
    left: 0;
    margin: 100px;

    h1 {

      padding: 50px;
      color: white;
      font-weight: normal;
      font-family: Verdana, Helvetica, sans-serif;
      font-size: 34px;
      text-align: left;
      line-height: 50px;

    }

    &::before {

      content: '';
      display: block;
      width: 4px;
      height: 165px;
      background-color: #fff;
      position: absolute;
      left: 0;
      bottom: 0;

    }

    &::after {

      content: '';
      display: block;
      width: 4px;
      height: 40px;
      left: 0;
      bottom: 0;
      position: absolute;
      background-color: #fff;
      transform: rotate(45deg);
      transform-origin: 0 100%;

    }
  }
}
